@import './settings/colors';

//base for all buttons
.btn {
    background-color: $color_white;
    &:hover {
        cursor: pointer;
    }
}

//home header buttons
.menu-top-button {
    // width: 98px;
    height: 36px;
    padding: 10px;
    margin: 1px;
    color: $color_greyish;
    font-size: 14px;
    font-weight: 400;
    border: 0.75px solid $color_white; 
    &:hover {
        border: 0.75px solid $color3_yellowish;    
    }
}

.menu-bottom-button {
    padding: 10px 20px;     
    margin: 1px; 
    border: 0.75px solid $color_white;   
    font-size: 18px; 
    font-weight: 400;  
    height: 42px;  
    background-color: transparent;
    &:hover {
     border: 0.75px solid $color_black;    
    } 
 }


.header-button {
    border: 0.75px solid $color_black; 
    text-transform: uppercase;
    // padding: 10px 30px;
    font-size: 36px;
    width: 310px;
    height: 120px;
    text-align: center;
}

.who-we-help--button {
    border: 0.75px solid $color_black;     
    padding: 10px 30px;
    font-size: 24px;
    font-weight: 400;
    width: 235px;
    text-align: center;
}

.form-submit-button {
    padding: 10px 20px;     
    margin: 1px; 
    border: 0.75px solid $color_black;   
    font-size: 18px;  
    font-weight: 300;
    background-color: $color_white;  
    // width: 180px;
    width: -moz-fit-content; 
    // &:hover {
    //  border: 1px solid $color_black;    
    // } 
 }

 .steps-button {
    padding: 10px 20px;     
    margin: 1px; 
    border: 0.75px solid $color_black;   
    font-size: 24px;  
    font-weight: 300;
    background-color: $color_white;  
    width: 180px;
    width: -moz-fit-content; 
 }

 .choice-button {
    padding: 10px 20px;     
    margin: 1px; 
    border: 0.75px solid $color_black;   
    font-size: 18px;  
    font-weight: 300;
    background-color: $color_white; 
    width: -moz-fit-content; 
    width: 200px;
    height: 60px;
    font-size: 16px;
 }

 .radio-choice-button {
     margin-right: 10px;
     width: 30px;
     height: 30px;
     border: 1px solid $color_black;
     border-radius: 2px;
     -webkit-border-radius: 2px;
     -moz-border-radius: 2px;
     -ms-border-radius: 2px;
     -o-border-radius: 2px;
     &:hover {
         background-color: $color3_yellowish;
     }
     &:focus {
        background-color: $color3_yellowish;
     }

}

.yellow-hover-btn {     
    border: 1px solid $color_black;
    padding: 10px;
    &:hover {
        background-color: $color3_yellowish;
    }
    &:focus {
        background-color: $color3_yellowish;
    }
}

// go to top icon
.AnyClassForContainer {
    position: fixed;
    right: -100px;
    bottom: 150px;
    transition: right 0.5s;
    cursor: pointer;
    background-color: white;
    font-size: 20px;
    padding: 10px;
  }
  
  .AnyClassForTransition {
    right: 20px;
  }

@media (max-width: 750px) {
    .header-button {
        font-size: 18px;
        width: 140px;
        height: 60px;
    }

    .who-we-help--button {
        font-size: 16px;
        width: 150px;
        height: 60px;
        margin: 2px;
    }

    .menu-bottom-button {
        width: 105vw;
        background-color: rgba(0, 0, 0, 0.8);
        color: $color_reddish;
    }

    .menu-top-button {
        margin-top: 10px;
        margin-bottom: 10px;
        width: 80px;
        font-size: 9px;
        border: 0.75px solid $color3_yellowish;
        &:hover {
            background-color: $color3_yellowish;
        }
        &:focus {
            background-color: $color3_yellowish;
        }
    }
}

@media (min-width: 560px) and (max-width: 750px) {

    .menu-bottom-button {
        width: 94vw;   
        &:hover {
            background-color: $color3_yellowish;           
        }
        &:focus {
            background-color: $color3_yellowish;            
        }
    }
  
}

@media (min-width: 560px) and (max-width: 1300px) { 
    .header-button {       
        font-size: 22px;
        width: 150px;
        height: 80px;          
    }
   
  
}

// iphone 5/SE
@media (min-width: 568px) and (max-width: 568px) {
    .menu-bottom-button {       
        font-size: 12px;       
        height: 30px;      
        display: flex;
        justify-content: center;
        align-items: center;
    }
}


